@charset "UTF-8";
/* resetCSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 20px;
}

@media print, screen and (max-width: 768px) {
  html {
    font-size: 16px;
  }
}

body {
  font-family: europa,"Yu Gothic",YuGothic,"ヒラギノ角ゴ ProN W3","Hiragino Kaku Gothic ProN","メイリオ",Meiryo,sans-serif;
}

.l-wrap {
  width: 100%;
  overflow: hidden;
}

.header {
  width: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  background-color: rgba(0, 0, 0, 0.5);
}

.header__bg {
  position: relative;
  z-index: 2;
  padding: 30px;
}

@media print, screen and (max-width: 768px) {
  .header__bg {
    padding: 10px;
  }
}

.header__logo a {
  font-family: cursive;
  display: block;
  text-decoration: none;
  font-size: 1.5rem;
  color: #FFFFFF;
}

@media print, screen and (max-width: 768px) {
  .header__logo a {
    font-size: 1.2rem;
  }
}

.header__cat {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.header__cat input,
.header__cat label {
  display: none;
}

@media print, screen and (max-width: 768px) {
  .header__cat input,
  .header__cat label {
    display: block;
  }
}

@media print, screen and (max-width: 768px) {
  .header__cat input {
    display: none;
  }
}

@media print, screen and (max-width: 768px) {
  .header__cat label {
    position: absolute;
    right: 10px;
    clear: both;
  }
}

.header__list {
  display: flex;
}

.header__item span {
  position: relative;
}

.header__item span::after {
  content: "";
  position: absolute;
  bottom: -10px;
  left: 50%;
  width: 0;
  height: 1px;
  background: #FFFFFF;
  opacity: 0;
  transition: all 0.8s cubic-bezier(0.19, 1, 0.22, 1);
  transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
}

.header__item:hover span:after {
  opacity: 1;
  left: 0;
  width: 100%;
}

@media print, screen and (max-width: 768px) {
  .header__navi {
    position: fixed;
    opacity: 0;
    background: rgba(255, 255, 255, 0.7);
    top: 50px;
    right: 0;
    bottom: 0;
    left: 0;
    transform: translateX(100%);
    z-index: -1;
    transition: all 1s ease-out;
  }
}

@media print, screen and (max-width: 768px) {
  .header__navi__wrap {
    width: 100%;
    background: #FFFFFF;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    transition-duration: 1s;
    transition-property: right;
    overflow-y: scroll;
  }
}

.header__navi__list {
  display: flex;
}

@media print, screen and (max-width: 768px) {
  .header__navi__list {
    margin: 0;
    display: block;
    justify-content: inherit;
  }
}

@media print, screen and (max-width: 768px) {
  .header__navi__item {
    border-bottom: 1px solid #000;
  }
}

@media print, screen and (max-width: 768px) {
  .header__navi__item a {
    padding: 0 50px;
    height: 100px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    color: #000;
    font-size: 24px;
    font-weight: 700;
    line-height: 1;
    transition: background-color 0.3s ease-in-out;
  }
}

.header__navi a {
  text-decoration: none;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 1rem;
  color: #FFFFFF;
}

@media print, screen and (max-width: 768px) {
  .hamburger__icon {
    display: inline-block;
    width: 35px;
    height: 25px;
  }
}

@media print, screen and (max-width: 768px) {
  .hamburger__icon i {
    position: absolute;
    width: 100%;
    height: 2px;
    display: block;
    background: #FFFFFF;
    transition: all 0.3s ease-in-out;
  }
}

.hamburger__icon i:nth-child(1) {
  top: 0;
}

.hamburger__icon i:nth-child(2) {
  margin: auto;
  top: 0;
  bottom: 0;
}

.hamburger__icon i:nth-child(3) {
  bottom: 0;
}

@media print, screen and (max-width: 768px) {
  .header__cat input:checked + label span.hamburger__icon i:nth-child(1) {
    margin: auto;
    transform: rotate(45deg);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
}

@media print, screen and (max-width: 768px) {
  .header__cat input:checked + label span.hamburger__icon i:nth-child(2) {
    transform: rotateX(90deg);
    top: 0;
    bottom: 0;
  }
}

@media print, screen and (max-width: 768px) {
  .header__cat input:checked + label span.hamburger__icon i:nth-child(3) {
    margin: auto;
    transform: rotate(-45deg);
    top: 0;
    right: 0;
    bottom: 0;
  }
}

@media print, screen and (max-width: 768px) {
  .header__cat input:checked ~ .header__navi {
    z-index: 99;
    opacity: 1;
    transform: translateX(0);
  }
}

.footer p {
